import { RouteProps } from "hoc/withRouter";
import { forwardRef, useEffect, useImperativeHandle, useRef, useState } from "react";

// --------- ---------- //
import { IAddressRef } from "components/Address";
import DocumentUploader, { IDocumentUploaderRef } from "components/DocumentUploader";
import InputBox from "components/InputBox";
import SaveComponent from "components/Save";
import SimpleDropDown from "components/SimpleDropDown";
import SkeletonCustom from "components/Skeleton";
import TextData from "components/TextData";

// ---------- ---------- //

// ---------- ---------- //
import SimpleReactValidator from "simple-react-validator";
import { initialLoanDocsInfo } from "../../Helper";

// ---------- ---------- //
import { getStandardValidator, messageReplace } from "utils/CommonUtil";

// ---------- ---------- //
import {
  DOCUMENT_CATEGORY,
  GST_REGISTRATION,
  INPUT_STYLE_TYPE,
  PAGE_MODE,
  PAGE_TYPE,
  YES_NO,
  YES_NO_OPTIONS,
} from "utils/Constant";

import SystemDatePicker from "components/SystemDatePicker/SystemDatePicker";
import { IOcrRelatedData } from "interface/common";

export interface IOrganisationDetailsRef {
  save: () => IOrganisationDetailsData | undefined;
  remove: () => IOrganisationDetailsData | undefined;
  onSaveSuccess: () => void;
  getData: () => IOrganisationDetailsData | undefined;
  resetData: () => void;
}

export interface IOrganisationDetailsData {
  uid?: string;
  isActive?: boolean;
  firmName: string;
  businessConstitution: string;
  websiteURL: string;
  emailId: string;
  empRegNo: string;
  udyamRegCertificateNo: string;
  incorporationDate: any;
  segment: string;
  noOfEmployees: string;
  ownershipOfBusiness: any;
  regUnderGST: any;
  GSTExemption: any;
  GSTReg: string;
  GSTIN: string;
  businessYearsInCurrentPremises: string;
  totalExpInYears: string;
  businessYearsInCurrentLocation: string;
  factoryBrances: any;
  grossMargin: string;
  netMargin: string;
  loanDocuments: any;
}

export const initialOrgBasicInfoData: IOrganisationDetailsData = {
  uid: "",
  isActive: true,
  firmName: "",
  businessConstitution: "",
  websiteURL: "",
  emailId: "",
  empRegNo: "",
  udyamRegCertificateNo: "",
  incorporationDate: "",
  segment: "",
  noOfEmployees: "",
  ownershipOfBusiness: "",
  regUnderGST: "",
  GSTExemption: "",
  GSTReg: "",
  GSTIN: "",
  businessYearsInCurrentPremises: "",
  totalExpInYears: "",
  businessYearsInCurrentLocation: "",
  factoryBrances: "",
  grossMargin: "",
  netMargin: "",
  loanDocuments: [initialLoanDocsInfo],
};

interface IProps extends RouteProps {
  data: IOrganisationDetailsData;
  isLoading?: boolean;
  disabled?: boolean;
  disabledSaveMenu?: boolean;
  ref?: any; //NOSONAR
  cardNumber?: number;
  dropDownOptions: any;
  pageType?: string;
  documentCategory?: string;
  ocrRelatedData?: IOcrRelatedData;
  onSaveOrganisationDetails: () => void;
  onRemoveOrganisationDetails?: () => void;
}

const OrganisationDetails = forwardRef<IOrganisationDetailsRef, IProps>((props, ref) => {
  const {
    data = initialOrgBasicInfoData,
    isLoading,
    cardNumber,
    dropDownOptions,
    pageType,
    disabled,
    disabledSaveMenu,
    documentCategory = DOCUMENT_CATEGORY.DOCUMENT_CATEGORY_BUSINESS_PROOF,
    ocrRelatedData,
    onSaveOrganisationDetails,
    onRemoveOrganisationDetails,
  } = props;

  // ----------------- states ---------------------- //
  const [pageObj, setPageObj] = useState<IOrganisationDetailsData>(data);
  const [pageMode, setPageMode] = useState<string>(PAGE_MODE.VIEW);

  const [timeOutLocal, setTimeOutLocal] = useState<any>(undefined);

  const [renderConfig, setRenderConfig] = useState<any>({
    GSTINValidationEnabled: false,
    GSTRegValidationEnabled: false,
  });

  // --------------------- ref ---------------------------- //

  const documentUploaderRef = useRef<IDocumentUploaderRef | null>(null);

  // --------------------------- validator initialization ---------------------- //

  const [, forceUpdate] = useState<boolean>(false); //NOSONAR

  const simpleValidator = useRef(
    new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    })
  );

  // --------------------------- global variables ---------------------- //

  const menuModel = [
    {
      className: "",
      label: "Remove",
      command: () => {
        onRemoveOrganisationDetails?.();
      },
      disabled: cardNumber === 0,
    },
  ];

  // --------------------------- global variables ---------------------- //

  /*
    --------------------------------------------------------------------------------------------------
    LifeCycle Methods: 
    --------------------------------------------------------------------------------------------------
  */

  useEffect(() => {
    setPageObj(() => ({ ...data }));
  }, [data]);

  useEffect(() => {
    if (pageObj?.regUnderGST && pageObj?.GSTExemption) {
      setRenderConfig((prevState: any) => {
        return {
          ...prevState,
          GSTINValidationEnabled:
            pageObj?.regUnderGST === YES_NO.YES && pageObj?.GSTExemption === YES_NO.NO,
          GSTRegValidationEnabled:
            pageObj?.regUnderGST === YES_NO.YES && pageObj?.GSTExemption === YES_NO.NO,
        };
      });

      resetValidator();
      simpleValidator?.current?.showMessages();
    }
  }, [pageObj?.regUnderGST, pageObj?.GSTExemption]);

  /*
    --------------------------------------------------------------------------------------------------
    Ref: For creating and returning properties
    --------------------------------------------------------------------------------------------------
  */
  const addressRef: any = useRef<IAddressRef | null>();

  useImperativeHandle(ref, () => ({
    save: onSave,
    onSaveSuccess: onSaveSuccess,
    remove: onRemove,
    getData: getData,
    resetData: resetData,
  }));

  /*
    -------------------------------------------------------------------------------------------------------
    Service Calls: For Getting Data from service/API
    -------------------------------------------------------------------------------------------------------
  */

  /* 
    -------------------------------------------------------------------------------------------------------
    Validators:
    -------------------------------------------------------------------------------------------------------
  */

  const validate = (e: any) => {
    simpleValidator?.current?.showMessageFor(e?.target?.name);
  };

  const validateForm = () => {
    let isValid = false;

    isValid = simpleValidator?.current?.allValid();
    if (isValid) {
      return isValid;
    } else {
      forceUpdate((prev) => !prev);
      simpleValidator?.current?.showMessages();
      forceUpdate((prev) => !prev);
      return false;
    }
  };

  /*
    -----------------------------------------------------------------------------------------------
    Handlers: Event handler for onChange, onBlur etc
    -----------------------------------------------------------------------------------------------
  */

  const handleChange = (e: any) => {
    // common state update
    setPageObj((prevPageObj: any) => {
      return {
        ...prevPageObj,
        [e.target.name]: e.target.value,
      };
    });
  };

  /*
    -----------------------------------------------------------------------------------------------
    on-Handlers: Event handler for onClick, onToggle etc
    -----------------------------------------------------------------------------------------------
  */

  const onSaveSuccess = () => setPageMode(PAGE_MODE.VIEW);

  const onEdit = () => setPageMode(PAGE_MODE.EDIT);

  const onRemove = () => {
    return { ...getData(), isActive: false };
  };

  const onSave = () => {
    const uploadedDocsInfo = documentUploaderRef?.current?.getUploadedData();

    if (!validateForm() || !uploadedDocsInfo?.documentUid) {
      // trigger uploader validations if uploader is mandatory
      return;
    }

    return getData();
  };

  const onCancel = () => {
    resetValidator();
    addressRef?.current?.resetValidator();
    documentUploaderRef?.current?.resetValidator();

    setPageMode(PAGE_MODE.VIEW);
  };

  /* 
    -------------------------------------------------------------------------------------------------
    Helper Functions:
    -------------------------------------------------------------------------------------------------
  */

  const debounce = (func: any, delay: any) => {
    clearTimeout(timeOutLocal);
    setTimeOutLocal(setTimeout(func, delay));
  };

  const resetValidator = () => {
    simpleValidator.current = new SimpleReactValidator({
      validators: { ...getStandardValidator() },
      ...messageReplace(),
      autoForceUpdate: { forceUpdate: () => forceUpdate((prev) => !prev) },
    });
    simpleValidator?.current?.hideMessages();
  };

  const getData = () => {
    const uploadedDocsInfo = documentUploaderRef?.current?.getUploadedData();
    documentUploaderRef?.current?.resetValidator();

    return { ...pageObj, loanDocumentsProof: uploadedDocsInfo };
  };

  const resetData = () => {
    setPageObj({ ...initialOrgBasicInfoData });
    setPageMode(PAGE_MODE.VIEW);
    resetValidator();
  };

  /* 
    ------------------------------------------------------------------------------------------------
    Sectional Render Functions: For Sub components / section wise renders
    ------------------------------------------------------------------------------------------------
  */

  const renderDocumentUploader = () => {
    return (
      <div className="flex flex-col space-y-4">
        <TextData
          label={"label.dde.corporate.businessProof*"}
          value="Attach Documents (PDF, JPG, JPEG or PNG)"
          titleClass="text-primary-sideBarBgColor text-sm font-bold mb-1"
        />

        <div>
          <DocumentUploader
            ref={documentUploaderRef}
            savedDocument={data.loanDocuments?.[0] ? data.loanDocuments[0] : {}}
            mode={pageMode}
            queryParamForDocList={documentCategory}
            ocrStageName={ocrRelatedData?.ocrStageName}
            ocrSubstageName={ocrRelatedData?.ocrSubstageName}
            ocrPayloadData={{
              loanUid: ocrRelatedData?.ocrPayloadData?.loanUid ?? "",
              applicant: {
                uid: ocrRelatedData?.ocrPayloadData?.applicant?.uid ?? "",
              },
            }}
            mandatoryDocUpload
          />
        </div>
      </div>
    );
  };

  const renderContent = () => {
    const {
      firmName,
      businessConstitution,
      websiteURL,
      emailId,
      empRegNo,
      udyamRegCertificateNo,
      incorporationDate,
      segment,
      noOfEmployees,
      ownershipOfBusiness,
      regUnderGST,
      GSTExemption,
      GSTReg,
      GSTIN,
      businessYearsInCurrentPremises,
      totalExpInYears,
      businessYearsInCurrentLocation,
      factoryBrances,
      grossMargin,
      netMargin,
    } = pageObj;

    const { GSTINValidationEnabled, GSTRegValidationEnabled } = renderConfig;

    return (
      <>
        <div className="grid grid-cols-1 sm:grid-cols-2  md:grid-cols-3 lg:grid-cols-4 gap-x-4 gap-y-5 ">
          <div className="col-span-1">
            <InputBox
              disabled={pageMode === PAGE_MODE.VIEW}
              variant={INPUT_STYLE_TYPE.SECONDARY}
              label={"label.organisation.details.nameOf.firm"}
              placeholder="Enter Name"
              name="firmName"
              className=""
              value={firmName}
              validation={simpleValidator?.current?.message(
                `firmName`,
                firmName,
                "mandatory|checkString"
              )}
              onBlur={validate}
              onChange={handleChange}
            />
          </div>

          <div className="col-span-1">
            <SimpleDropDown
              label={"label.organisation.details.constitution.business"}
              placeholder="Select Constitution"
              name="businessConstitution"
              value={businessConstitution}
              onChange={handleChange}
              validation={simpleValidator?.current?.message(
                "businessConstitution",
                businessConstitution,
                "mandatory"
              )}
              onBlur={validate}
              disabled={pageMode === PAGE_MODE.VIEW}
              options={dropDownOptions.BUSINESS_CONSTITUTION_OPTION}
              optionLabel="name"
              optionValue="code"
            />
          </div>

          <div className="col-span-1">
            <InputBox
              variant={INPUT_STYLE_TYPE.SECONDARY}
              label={"label.organisation.details.website.URL"}
              placeholder="Enter Name"
              name="websiteURL"
              className=""
              value={websiteURL}
              validation={simpleValidator?.current?.message(`websiteURL`, websiteURL, "TLDCheck")}
              onBlur={validate}
              disabled={pageMode === PAGE_MODE.VIEW}
              onChange={handleChange}
            />
          </div>

          <div className="col-span-1">
            <InputBox
              variant={INPUT_STYLE_TYPE.SECONDARY}
              label={"label.organisation.details.emailId"}
              placeholder="Enter email Id"
              name="emailId"
              value={emailId}
              onChange={handleChange}
              validation={simpleValidator?.current?.message(`emailId`, emailId, "email")}
              onBlur={validate}
              disabled={pageMode === PAGE_MODE.VIEW}
            />
          </div>

          <div className="col-span-1">
            <InputBox
              variant={INPUT_STYLE_TYPE.SECONDARY}
              label={"label.organisation.details.employment.registration/license.No"}
              placeholder="Enter No."
              name="empRegNo"
              className=""
              value={empRegNo}
              onChange={handleChange}
              validation={simpleValidator?.current?.message(`empRegNo`, empRegNo, "mandatory")}
              onBlur={validate}
              disabled={pageMode === PAGE_MODE.VIEW}
            />
          </div>

          <div className="col-span-1">
            <InputBox
              variant={INPUT_STYLE_TYPE.SECONDARY}
              label={"label.organisation.details.udyam.registration.certificate.No"}
              placeholder="Enter No."
              value={udyamRegCertificateNo}
              name="udyamRegCertificateNo"
              className=""
              onChange={handleChange}
              disabled={pageMode === PAGE_MODE.VIEW}
            />
          </div>

          <div className="col-span-1">
            <SystemDatePicker
              disabled={pageMode === PAGE_MODE.VIEW}
              variant={INPUT_STYLE_TYPE.SECONDARY}
              label={"label.organisation.details.incorporation.date"}
              placeholder="Select Date"
              name="incorporationDate"
              value={incorporationDate ? new Date(incorporationDate) : ""}
              onChange={handleChange}
              validation={simpleValidator?.current?.message(
                `incorporationDate`,
                incorporationDate,
                "mandatory"
              )}
            />
          </div>

          <div className="col-span-1">
            <SimpleDropDown
              label={"label.organisation.details.segment"}
              placeholder="Select Segment"
              name="segment"
              value={segment}
              onChange={handleChange}
              validation={simpleValidator?.current?.message("segment", segment, "mandatory")}
              onBlur={validate}
              disabled={pageMode === PAGE_MODE.VIEW}
              options={dropDownOptions.SEGMENT_OPTION}
              optionLabel="name"
              optionValue="code"
            />
          </div>

          <div className="col-span-1">
            <SimpleDropDown
              label={"label.organisation.details.noOfEmployees"}
              placeholder="Enter No of Employees"
              value={noOfEmployees}
              name="noOfEmployees"
              onChange={handleChange}
              validation={simpleValidator?.current?.message(
                `noOfEmployees`,
                noOfEmployees,
                "mandatory"
              )}
              onBlur={validate}
              disabled={pageMode === PAGE_MODE.VIEW}
              options={dropDownOptions.NO_EMPLOYEE_OPTION}
              optionLabel="name"
              optionValue="code"
            />
          </div>

          <div className="col-span-1">
            <SimpleDropDown
              label={"label.organisation.details.ownership.business"}
              placeholder="Select"
              name="ownershipOfBusiness"
              value={ownershipOfBusiness}
              onChange={handleChange}
              validation={simpleValidator?.current?.message(
                "ownershipOfBusiness",
                ownershipOfBusiness,
                "mandatory"
              )}
              onBlur={validate}
              disabled={pageMode === PAGE_MODE.VIEW}
              options={dropDownOptions.OWNERSHIP_OPTION}
              optionLabel="name"
              optionValue="code"
            />
          </div>

          <div className="col-span-1">
            <SimpleDropDown
              label={"label.organisation.details.registered.GST*"}
              placeholder="Yes/No"
              name="regUnderGST"
              value={regUnderGST}
              onChange={handleChange}
              disabled={pageMode === PAGE_MODE.VIEW}
              options={YES_NO_OPTIONS}
              optionLabel="label"
              optionValue="value"
              validation={simpleValidator?.current?.message(
                "regUnderGST",
                regUnderGST,
                "mandatory"
              )}
            />
          </div>

          <div className="col-span-1">
            <SimpleDropDown
              label={"label.organisation.details.GST.exemption.mandatory"}
              placeholder="Select"
              name="GSTExemption"
              value={GSTExemption}
              onChange={handleChange}
              disabled={pageMode === PAGE_MODE.VIEW}
              options={YES_NO_OPTIONS}
              optionLabel="label"
              optionValue="value"
              onBlur={validate}
              validation={simpleValidator?.current?.message(
                "GSTExemption",
                GSTExemption,
                "mandatory"
              )}
            />
          </div>

          <div className="col-span-1">
            <SimpleDropDown
              label={
                GSTRegValidationEnabled
                  ? "label.organisation.details.GST.registration.mandatory"
                  : "label.organisation.details.GST.registration"
              }
              placeholder="Select"
              value={GSTReg}
              name="GSTReg"
              onChange={handleChange}
              disabled={pageMode === PAGE_MODE.VIEW}
              options={GST_REGISTRATION}
              optionLabel="label"
              optionValue="value"
              onBlur={GSTRegValidationEnabled ? validate : undefined}
              validation={
                GSTRegValidationEnabled
                  ? simpleValidator?.current?.message("GSTReg", GSTReg, "mandatory")
                  : undefined
              }
            />
          </div>

          <div className="col-span-1">
            <InputBox
              disabled={pageMode === PAGE_MODE.VIEW}
              variant={INPUT_STYLE_TYPE.SECONDARY}
              label={
                GSTINValidationEnabled
                  ? "label.organisation.details.GSTIN.mandatory"
                  : "label.organisation.details.GSTIN"
              }
              placeholder="Enter GSTIN"
              name="GSTIN"
              className=""
              value={GSTIN}
              onChange={handleChange}
              onBlur={GSTINValidationEnabled ? validate : undefined}
              validation={
                GSTINValidationEnabled
                  ? simpleValidator?.current?.message(
                      "GSTIN",
                      GSTIN,
                      "mandatory|alpha_num|min:15|max:15"
                    )
                  : simpleValidator?.current?.message(
                    "GSTIN",
                    GSTIN,
                    "alpha_num|min:15|max:15"
                  )
              }
            />
          </div>

          <div className="col-span-1">
            <InputBox
              disabled={pageMode === PAGE_MODE.VIEW}
              variant={INPUT_STYLE_TYPE.SECONDARY}
              label={"label.organisation.details.years.current.business"}
              placeholder="Enter No. of years"
              name="businessYearsInCurrentPremises"
              className="text-right"
              value={businessYearsInCurrentPremises}
              onChange={handleChange}
              onBlur={validate}
              validation={simpleValidator?.current?.message(
                "businessYearsInCurrentPremises",
                businessYearsInCurrentPremises,
                "checkNumber|min:0,num"
              )}
            />
          </div>

          <div className="col-span-1">
            <InputBox
              disabled={pageMode === PAGE_MODE.VIEW}
              variant={INPUT_STYLE_TYPE.SECONDARY}
              label={"label.organisation.details.total.work.experience"}
              placeholder="Enter Total no of years"
              name="totalExpInYears"
              className="text-right"
              value={totalExpInYears}
              onChange={handleChange}
              validation={simpleValidator?.current?.message(
                `totalExpInYears`,
                totalExpInYears,
                "mandatory|checkNumber|min:0,num"
              )}
              onBlur={validate}
            />
          </div>

          <div className="col-span-1">
            <InputBox
              disabled={pageMode === PAGE_MODE.VIEW}
              variant={INPUT_STYLE_TYPE.SECONDARY}
              label={"label.organisation.details.years.business.city/area"}
              placeholder="Enter No of Years"
              name="businessYearsInCurrentLocation"
              className="text-right"
              value={businessYearsInCurrentLocation}
              onChange={handleChange}
              validation={simpleValidator?.current?.message(
                `businessYearsInCurrentLocation`,
                businessYearsInCurrentLocation,
                "mandatory|checkNumber|min:0,num"
              )}
              onBlur={validate}
            />
          </div>

          <div className="col-span-1">
            <SimpleDropDown
              label={"label.organisation.details.factory.branches"}
              placeholder="Select"
              value={factoryBrances}
              name="factoryBrances"
              onChange={handleChange}
              disabled={pageMode === PAGE_MODE.VIEW}
              options={YES_NO_OPTIONS}
              optionLabel="label"
              optionValue="value"
            />
          </div>

          <div className="col-span-1">
            <InputBox
              disabled={pageMode === PAGE_MODE.VIEW}
              variant={INPUT_STYLE_TYPE.SECONDARY}
              label={"label.organisation.details.estimated.gross.margin"}
              placeholder="Enter Gross Margin"
              name="grossMargin"
              className="text-right"
              value={grossMargin}
              onChange={handleChange}
              validation={simpleValidator?.current?.message(
                `grossMargin`,
                grossMargin,
                "mandatory|checkNumber|min:0,num|max:100,num|maxDecimal:2"
              )}
              onBlur={validate}
            />
          </div>

          <div className="col-span-1">
            <InputBox
              disabled={pageMode === PAGE_MODE.VIEW}
              variant={INPUT_STYLE_TYPE.SECONDARY}
              label={"label.organisation.details.net.margin"}
              placeholder="Enter Net Margin"
              name="netMargin"
              className="text-right"
              value={netMargin}
              onChange={handleChange}
              validation={simpleValidator?.current?.message(
                `netMargin`,
                netMargin,
                "mandatory|checkNumber|min:0,num|max:100,num|maxDecimal:2"
              )}
              onBlur={validate}
            />
          </div>
        </div>
        {renderDocumentUploader()}
      </>
    );
  };

  const renderCorporateGuarantorContent = () => {
    const {
      firmName,
      businessConstitution,
      websiteURL,
      emailId,
      empRegNo,
      udyamRegCertificateNo,
      incorporationDate,
    } = pageObj;

    return (
      <>
        <div className="grid grid-cols-1 sm:grid-cols-2  md:grid-cols-3 lg:grid-cols-4 gap-x-4 gap-y-5 ">
          <div className="col-span-1">
            <InputBox
              disabled={pageMode === PAGE_MODE.VIEW}
              variant={INPUT_STYLE_TYPE.SECONDARY}
              label={"label.organisation.details.nameOf.firm"}
              placeholder="Enter Name"
              name="firmName"
              className=""
              value={firmName}
              validation={simpleValidator?.current?.message(
                `firmName`,
                firmName,
                "mandatory|checkString"
              )}
              onBlur={validate}
              onChange={handleChange}
            />
          </div>

          <div className="col-span-1">
            <SimpleDropDown
              label={"label.organisation.details.constitution.business"}
              placeholder="Select Constitution"
              name="businessConstitution"
              value={businessConstitution}
              onChange={handleChange}
              validation={simpleValidator?.current?.message(
                "businessConstitution",
                businessConstitution,
                "mandatory"
              )}
              onBlur={validate}
              disabled={pageMode === PAGE_MODE.VIEW}
              options={dropDownOptions.BUSINESS_CONSTITUTION_OPTION}
              optionLabel="name"
              optionValue="code"
            />
          </div>

          <div className="col-span-1">
            <InputBox
              variant={INPUT_STYLE_TYPE.SECONDARY}
              label={"label.organisation.details.website.URL"}
              placeholder="Enter Name"
              name="websiteURL"
              className=""
              value={websiteURL}
              validation={simpleValidator?.current?.message(`websiteURL`, websiteURL, "TLDCheck")}
              onBlur={validate}
              disabled={pageMode === PAGE_MODE.VIEW}
              onChange={handleChange}
            />
          </div>

          <div className="col-span-1">
            <InputBox
              variant={INPUT_STYLE_TYPE.SECONDARY}
              label={"label.organisation.details.emailId"}
              placeholder="Enter email Id"
              name="emailId"
              value={emailId}
              onChange={handleChange}
              validation={simpleValidator?.current?.message(`emailId`, emailId, "email")}
              onBlur={validate}
              disabled={pageMode === PAGE_MODE.VIEW}
            />
          </div>

          <div className="col-span-1">
            <InputBox
              variant={INPUT_STYLE_TYPE.SECONDARY}
              label={"label.organisation.details.employment.registration/license.No"}
              placeholder="Enter No."
              name="empRegNo"
              className=""
              value={empRegNo}
              onChange={handleChange}
              validation={simpleValidator?.current?.message(`empRegNo`, empRegNo, "mandatory")}
              onBlur={validate}
              disabled={pageMode === PAGE_MODE.VIEW}
            />
          </div>

          <div className="col-span-1">
            <InputBox
              variant={INPUT_STYLE_TYPE.SECONDARY}
              label={"label.organisation.details.udyam.registration.certificate.No"}
              placeholder="Enter No."
              value={udyamRegCertificateNo}
              name="udyamRegCertificateNo"
              className=""
              onChange={handleChange}
              disabled={pageMode === PAGE_MODE.VIEW}
            />
          </div>

          <div className="col-span-1">
            <SystemDatePicker
              disabled={pageMode === PAGE_MODE.VIEW}
              variant={INPUT_STYLE_TYPE.SECONDARY}
              label={"label.organisation.details.incorporation.date"}
              placeholder="Select Date"
              name="incorporationDate"
              value={incorporationDate ? new Date(incorporationDate) : ""}
              onChange={handleChange}
              validation={simpleValidator?.current?.message(
                `incorporationDate`,
                incorporationDate,
                "mandatory"
              )}
            />
          </div>
        </div>

        {renderDocumentUploader()}
      </>
    );
  };

  /* 
    -----------------------------------------------------------------------------------------
    render: The Main return()
    -----------------------------------------------------------------------------------------
  */

  return (
    <div className="">
      {isLoading ? (
        <SkeletonCustom height="19.375rem" width="100%" borderRadius="8px" animation="wave" />
      ) : (
        <SaveComponent
          disabled={disabled}
          disabledMenu={disabledSaveMenu}
          title={"label.dde.organisation.details.basic.information"}
          titleArgs={{ count: cardNumber }}
          pageMode={pageMode}
          onCancel={onCancel}
          onEdit={onEdit}
          onSave={onSaveOrganisationDetails}
          kebabModel={menuModel}
        >
          {pageType === PAGE_TYPE.CORPORATE_GUARANTOR
            ? renderCorporateGuarantorContent()
            : renderContent()}
        </SaveComponent>
      )}
    </div>
  );
});

OrganisationDetails.defaultProps = {
  disabled: false,
};

export default OrganisationDetails;
